
@use '../../../scss/vars' as vars;
.modal, .overlay{
    width: 100%;
    height: 100%;
    top:0;
    left:0;
    right:0;
    bottom:0;
    position: fixed;
    z-index: 999999;
}
.create-post-container{
    width:50%;
    height: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    display: flex;
    padding: 0rem 1rem;
    background-color: vars.$dark-1;
    @media  only screen and (max-width: 80em) {
        width: 90%;
        height: 40%;
        padding: 1rem 1rem;
    }
    @media  only screen and (max-width: 56.25em) {
        width: 90%;
        height: fit-content;
        padding: 1rem 1rem;
    }
    &--banner{
        background-image: url("../../../svgs/composing.svg");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        width: 30%;
        height: 100%;
        @media  only screen and (max-width: 56.25em) {
            display: none;
        }
    }
    &__right-side{
        width: 70%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        @media  only screen and (max-width: 56.25em) {
            width: 100%;
        }
        &__input-container{
        }
    }
    &__header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        &--title{
            color: vars.$primary;
            font-size: 2rem;
            font-weight: 600;
        }
        &--close-btn{
            position: relative;
            left: 0;
            top: 0;
           font-size: 2rem;
            cursor: pointer;

        }
    }


}