

@use './../../scss/vars' as vars;
.page__profile{
  background-color: vars.$dark-2;

  min-height: 100vh;
}
.profile{
  padding-top: 4.4rem;
  width: 100%;

  &__banner{
    width: 100%;
    &--img{
      width: 100%;
      height: 50rem;
      object-fit: cover;

      @media only screen and (max-width: 800px) {
        height: 30rem;
      }
      @media only screen and (max-width: 550px) {
        height: 15rem;
      }
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
    background-color: vars.$dark-3;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: inset 0px 1px 0px 0px rgb(0 0 0 / 20%);
    gap:10rem;
    padding: 1rem;
    align-items: center;
    @media only screen and (max-width: 750px) {
      gap: 5rem;
    }
    @media only screen and (max-width: 500px) {
      gap: 2rem;
    }
    @media only screen and (max-width: 450px) {
      gap: 1rem;
    }



    &__mini-profile{

      //display only on mobile
        @media only screen and (min-width: 751px) {
            display: none;
        }
      &--img{
        height:6rem;
        width: 6rem;
        object-fit: cover;
        margin: 0;
      }
    }
    &__links{
      display: flex;
      gap: 2rem;
      &__link{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: .5rem;
        cursor: pointer;

        &--text{
          font-size: 1.4rem;
          font-weight: 600;
          text-decoration: none;
          text-transform: uppercase;
          color: grey;
        }
        &--number{
          font-size: 1.2rem;
          font-weight: 400;
          color: gray;
          text-decoration: none;
        }
      }
    }
    &__actions{
      display: flex;
      align-self: end;
      justify-self: end;
      gap: 1rem;

      &--follow-btn{
        border: none;
        padding: 1.3rem;
        background-color: vars.$primary;
        border-radius: 7px;
        width: 10rem;
        &:hover{
          //GLOW
            box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.75);
        }
      }
      &--followed-btn{
        border: none;
        padding: 1.3rem;
        background-color: vars.$dark-1;
        border-radius: 7px;
        width: 10rem;
        &:hover{
          //GLOW
            box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.75);
        }
      }
      &--message{
        cursor: pointer;
        background: url("../../svgs/phone-msg.svg") no-repeat;
        background-size: contain;
        width: 1.5rem;
        height: 1.5rem;
        padding: 1rem;
      }
      &--dropdown-icon{
        cursor: pointer;
        padding: 1rem;
        width: 2rem;
        height: 2rem;
        background: url("../../svgs/settings.svg") no-repeat ;
        background-size: contain;

        &:hover .profile__nav__actions__dropdown{
          display: block;
        }
      }
      &__dropdown{
        min-height: 4.3rem;
        display: none;
        position: relative;
        top: 2.8rem;

        left: -1rem;
        z-index: 1;
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
        min-width: 10rem;
        width: 20rem;
        &__item{
          display: flex;
          align-items: center;
          justify-items: center;
          padding: 1rem;
          background-color: white;
          &:hover{
            background-color: lightgray;
            cursor: pointer;
          }
          &--icon{
            width: 3rem;
            height: 3rem;
            &---block{
              background: url("../../svgs/block.svg") no-repeat;
              background-size: contain;

            }
          }
        }
        & span{
            display: block;
            padding: 1rem;
            text-decoration: none;
            color: black;
            font-size: 1rem;
        }
      }
    }
  }

  &__body{
    display:flex;
    justify-content: center;
    gap:4rem;
    width: 100%;
    background-color: vars.$dark-2;

    @media only screen and (max-width:750px) {
      flex-direction: column;
      align-items: center;
    }
    &__left{
      @media only screen and (max-width: 750px) {
        width: 100%;
      }
      &__user{
        &__images{
          width: 100%;
          display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-template-rows: auto;
            row-gap: .8rem;
            column-gap: .3rem;
            justify-content: center;
          align-items: center;
        }
        &--img{
          position: relative;
          bottom: 17rem;
          border: vars.$dark-3 solid 0.4rem;
          border-radius: 0.5rem;
          cursor: pointer;
          width: 20rem;
          height: 20rem;
          object-fit: cover;

          @media only screen and (max-width: 750px) {
            display: none;
          }

        }
        &--small-image-link{

        }
        &--small-image{
          border: vars.$dark-3 solid 0.4rem;
          border-radius: 0.5rem;
          cursor: pointer;
          width: 8.5rem;
          height: 8.5rem;
          object-fit: cover;
        }

        &__info{
          display: flex;
          position: relative;
          flex-direction: column;
          bottom: 17rem;
          //border: vars.$dark-3 solid 1px;
          //background-color: vars.$dark-1;
          padding: 1rem;box-sizing: border-box;
          @media only screen and (max-width: 750px) {
            bottom: 0;
            width: 100%;
          }

          & span{
            color: black;
            font-size: 1.5rem;

          }
          &__names{
            display: flex;
            flex-direction: column;


          }
          &__additional-info{
            display: flex;
            flex-direction: column;
            width: 30rem;
            @media only screen and (max-width: 750px) {
              width: 100%;
            }
            &--website{
              cursor: pointer;
            }
            &--container{
              display:flex;
              gap:1rem;
              align-items: center;
            }
          }
        }
      }
    }

    &__center{
      width: 59rem;
      margin-top: 1rem;
      border: vars.$dark-3 solid 1px;
      height: fit-content;

      @media only screen and (max-width:750px) {
        width: 98%;
      }

      &__header{
        display: flex;
        gap: 1rem;
        background-color: vars.$dark-3;
        border: rgba(0, 0, 0, 0.1) solid 0.1rem;
        padding: 1rem;
        width: 100%;
        box-sizing: border-box;
        &--text{
            font-size: 2.5rem;
          text-decoration: none;
          color: vars.$light-1;
        }
      }
    }
    &__right{
      // display: none on tablets
      @media only screen and (max-width: 85em) {
        display: none;
      }
    }

  }
}
.profile-info-box{
  border: vars.$dark-3 solid 1px;
  background-color: vars.$dark-1;
  padding: 1rem .8em;
  box-sizing: border-box;
border-radius: 7px;


}
.timeline--loading-container{
  min-height: 100vh;
  background-color: vars.$dark-2;
}