@use '../../../scss/vars' as vars;
@use '../../../scss/typography' as typography;

.forgot-password{
    margin-top: 4.4rem;
    width: 100%;
    height: calc(100vh - 4.4rem);
    display: flex;
    flex-direction: column;
    align-items: center;
justify-content: center;
    background-color: vars.$dark-3;
    @media screen and (max-width: 768px) {
        height: 100vh;
    }
    &__container{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: fit-content;
        height: fit-content;
        background-color: vars.$dark-2;
        padding: 4rem 6rem;
        border-radius: 1rem;
        //shaddow
        -webkit-box-shadow: 0px 0px 15px 0px rgba(255, 255, 255, 0.75);
        box-sizing: border-box;
        @media screen and (max-width: 768px) {
            //make card almost full width and height
            width: 90%;
            height: 90%;
            padding: 2rem 3rem;
        }
        &__body{
            width: 100%;
        }

    }


}
h3{
    @include typography.supporting-text;
    color: vars.$primary;

}
label{
    @include typography.supporting-text;
    color: vars.$light-1;
    margin-right: 0.5rem;
}
input{
    @include typography.supporting-text;
    background-color: vars.$dark-1;
    color: vars.$light-1;
    border: none;
    border-radius: 0.5rem;
    padding: 1rem 1.5rem;
    box-sizing: border-box;
    margin-top: 1rem;
    width: 100%;
    &:focus{
        outline: none;
    }
}
button{
    @include typography.heading-text;
    background-color: vars.$primary;
    color: white;
    border: none;
    border-radius: 0.5rem;
    padding: 0.5rem 1rem;
    margin-top: 1rem;
    cursor: pointer;
    &:hover{
        background-color: vars.$primary-hovered;
    }
}
//success message
h4{
    @include typography.supporting-text;
    color: vars.$success
}


//error message
h5{
    @include typography.supporting-text;
    color: vars.$error;
}