@use './vars.scss' as vars;
.user-profile{
    &__image{
        width: 4rem;
        height: 4rem;
        object-fit: cover;
    }
    &__dropdown{

        display: none;
        flex-direction: column;
        gap:2rem;
        position: absolute;
        padding: 2rem;

        background-color: vars.$dark-2;
        min-width:80%;
        min-height: fit-content;
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
        z-index: 1000;
        //top background 30% image
        &--top-bg{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 30%;
            border-radius: 7px;
            //background-image: url('./../imgs/banner.png');
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
        }
        &--avatar-img{
            height: 7rem;
            width: 7rem;
            position: relative;
            left: calc(50% - 3.5rem);
            border-radius: 7px;

            object-fit: cover;

        }

        & a{
            color: black;
            text-align: center;
            border-radius: 7px;
            background-color: vars.$primary;
            padding: 12px 16px;
            text-decoration: none;
            font-weight: 600;
            font-size: 1.4rem;
            display: block;
        }
    }
    &__line{
        width: 100%;
        height: 0.1rem;
        background-color: rgba(0, 0, 0, 0.1);
    }

}
.user-profile-dropdown-container{
    height: 4.3rem;
    position: relative;
    width: 100%;
    &:hover .user-profile__dropdown{
        display: flex;
    }
    & a:hover {
        background-color: vars.$primary-hovered;
    }
}
