.modal, .overlay{
  width: 100%;
  height: 100%;
  top:0;
  left:0;
  right:0;
  bottom:0;
  position: fixed;
  z-index: 999999;
}
.container{
  display: flex;
  flex-direction: column;
  background-color: #292f33;
  width: 60rem;
  height: fit-content;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  &__header{
    display: flex;
    margin-bottom: 1rem;
    margin-top: 1rem;
    &--close-btn{
      cursor: pointer;
      font-size: 3rem;
      position: relative;
      color:white;
      top:0;
      left: 96%;
    }
    &--title{
        font-size: 2.5rem;
        color: white;
      margin:auto;

    }
  }
  &__form{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    &__inputs{

      &__labels{
        position: relative;
        &--banner{
            width: 100%;
          &--button{
            width: 100% ;
            height: 10rem;
            background-size: cover;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            &::after{
              content:"";
              display: block;
              width: 5rem;
                height: 4rem;
              background: url("../../../svgs/add_circle_white.svg") no-repeat;
              background-size: contain;
              opacity: 0;
                transition: all 0.3s ease;
            }
            &:hover{
              cursor: pointer;
              opacity: 0.8;
                &::after{
                  cursor: pointer;
                  opacity: 0.8;
                }

            }
          }
          &--input{
            display: none;
          }
        }
        &--avatar{
          position: absolute;
          top: 7rem;
            left:10rem;
          &--button{
            width: 10rem;
            height: 10rem;
            border-radius: 50%;
            border: white .4rem solid;

            background: no-repeat;
            background-size: cover;

            display: flex;
            justify-content: center;
            align-items: center;
            &::after{
                content:"";
                display: block;
                width: 5rem;
                    height: 5rem;
                background: url("../../../svgs/add_circle_white.svg") no-repeat;
                background-size: contain;
                opacity: 0;
                    transition: all 0.3s ease;
            }
            &:hover{
              cursor: pointer;
              opacity: 0.8;
              &::after{
                opacity: 1;
              }
            }

          }
          &--input{
                display: none;
          }
        }
      }
      &--input-text{
        width: 90%;
        font-weight: 700;
        font-size: 1.4em;
        margin-top: 1rem;
        padding: 10px;
        border-width: 2px;
        border-color: rgba(247, 247, 247, .3);
        border-style: solid;
        &:focus{
          background: white;
          transition: all 0.3s ease;
          color: #222;
          outline: none;
        }
      }
      &--submit-btn{
        font-weight: 700;
        font-size: 1.8em;
        color: #111;
        background: #fefefe;
        box-shadow: 0px 4px 0px 0px #d26a60;
        border-style: none;
        padding: 10px 50px;
        margin: 25px 0 15px 0;
        position: relative;
        display: inline-block;
        transition: all .1s linear;
        &:hover{
          top: 2px;
          box-shadow: 0px 2px 0px 0px #d26a60;
        }
        &:active{
          box-shadow: 0 2px 0 #d26a60;
          transform: translateY(3px);
        }
      }
    }

  }
}