@use './../../../scss/vars.scss' as vars;
.not-found{
  width: 100vw;
    height: 100vh;
  background-color: vars.$dark-2;
  display: flex;
    flex-direction: column;
  &--404{
    width: 50%;
    height: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    &--img{
      background-image: url("./../../../svgs/404.svg");
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      width: 100%;
      height: 100%;
    }
    &--text{
        color: vars.$light-1;
        font-size: 2rem;
        text-align: center;
        margin-top: 1rem;
        margin-bottom: 1rem;
    }
    &--text-important{
      display: inline-block;
        color: vars.$primary;
        font-size: 2.5rem;
        text-align: center;
        margin-top: 1rem;
        margin-bottom: 1rem;
      transition: ease-in-out .1s;
      cursor: pointer;
      &:hover{

        //increase size
        transform: scale(1.2);

      }
    }
  }
}


.error-icon-credit{
    position: absolute;
    bottom: 0;
    right: 0;
    color: vars.$light-1;
    font-size: 0.8rem;
    a{
        color: vars.$light-1;
        text-decoration: none;
        &:hover{
        text-decoration: underline;
        }
    }
}