.modal, .overlay{
  width: 100%;
  height: 100%;
  top:0;
  left:0;
  right:0;
  bottom:0;
  position: fixed;
  z-index: 999999;
}
.register-container, .login-container{
  display: flex;
  width: 70%;
  height: 80%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  &--banner{
    width: 50%;
    height: 100%;
    background: url("../../../imgs/auth-banner.jpg");
    background-size: cover;
  }

  &__form{
    width: 50%;
    height: 100%;
    background-color: #292f33;

    &__inputs{
      display: flex;
        flex-direction: column;
      align-items: center;
      gap: 2rem;

      &--input{
        width: 90%;

        font-weight: 700;
        font-size: 1.4em;
        padding: 10px;
        border-width: 2px;
        border-color: rgba(247, 247, 247, .3);
        border-style: solid;
        &:focus{
          background: white;
          transition: all 0.3s ease;
          color: #222;
          outline: none;
        }
      }

    }
    &--submit-btn{
      font-weight: 700;
      font-size: 1.8em;
      color: #111;
      background: #fefefe;
      box-shadow: 0px 4px 0px 0px #d26a60;
      border-style: none;
      padding: 10px 50px;
      margin: 25px 0 15px 0;
      position: relative;
      display: inline-block;
      transition: all .1s linear;
      &:hover{
        top: 2px;
        box-shadow: 0px 2px 0px 0px #d26a60;
      }
      &:active{
        box-shadow: 0 2px 0 #d26a60;
        transform: translateY(3px);
      }
    }
    &--header{
      margin-top: 5rem;
      text-align: center;
      &--title{
        font-size: 3em;
        color: #fefefe;
        margin-top: 0;
      }
      &--subtitle{
            font-size: 1.5em;
            color: #fefefe;
        margin-top: 0;

      }
      &--close-btn{
        cursor: pointer;
        font-size: 3rem;
        position: relative;

        color:white;
        top: -4rem;
        left: 35rem;

      }
    }
    &--footer{
     display: flex;
      justify-content: center;
      gap: .5rem;
      &--text{
        color: #fefefe;
        font-size: 1.2em;
        text-align: center;
        margin-top: 2rem;
      }
      &--link{
        color: #fefefe;
        font-size: 1.2em;
        text-align: center;
        margin-top: 2rem;
        cursor: pointer;
        &:hover{
          text-decoration: underline;
        }
      }

    }


  }
}